h1 {
  font-size: 1.5rem;
}

.App {
  font-family: sans-serif;
  text-align: center;
  color: black;
  width: 100%;
}

.image {
  border-radius: 50%;
  width: 100px;
}

.btn-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 30px 20px;
}

.btn {
  padding: 12px;
  border: 1px solid #eaecf0;
  background-color: #eaecf0;
  font-size: 0.9em;
  font-weight: 700;
  margin-top: 5px;
  cursor: pointer;
  border-radius: 5px;
}

.btn a {
  text-decoration: none;
  color: #000;
}

.btn:hover {
  margin-right: -8px;
  transition: all 0.3s ease-in;
}

#slack {
  display: none;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

@media (max-width: 780px) {
  .footer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
}

.logo2 {
  width: 150px;
}

.logo1 {
  width: 90px;
}
